<template>
  <el-image
    class="layout-list-item-img"
    style="width: 100%"
    :src="base64"
    lazy
    :error="error"
    fit="cover"
  ></el-image>
</template>

<script>
import axios from "axios";
export default {
  name: "AppLazyImg",
  data() {
    return {
      base64: "",
      site: process.env.VUE_APP_PROXY_SITE,
    };
  },
  props: {
    src: {
      type: String,
      default: "",
    },
  },
  methods: {
    error() {
      console.log(`${this.site}${this.src}`);
      axios
        .get(`${this.site}${this.src}`)
        .then((res) => {
          this.base64 = res.data;
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  created() {
    this.error();
  },
};
</script>
<style scoped lang="scss"></style>
